@import 'npm:cssgram/source/css/cssgram.css';

.instant-print {
  position: relative;
  display: inline-block;
  margin: .5em;
  padding: 1.25rem 1.25rem 5rem 1.25rem;
  background: url('data:image/jpeg;base64,');
  border-radius: 1px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.instant-print figure {
  margin: 0;
  background-color: #fff;
}

.instant-print figure img {
  opacity: 0;
  transition-property: opacity;
  transition-delay: .5s;
  transition-duration: 1s;
  transition-timing-function: linear;
}

.instant-print figure img.instant-print--loaded {
  opacity: 1;
}

.instant-print figcaption {
  position: absolute;
  bottom: 2rem;
  width: calc(100% - 3rem);
  color: rgba(0,0,0,0.8);
  font-family: cursive;
  font-size: 1.5rem;
  font-style: italic;
  font-weight: bold;
  text-align: center;
  line-height: 1;
}
